.contacts {
  // padding: 56px 120px 70px 88px;
  padding-left: 25%;

  @media (max-width: $vp-1199) {
    padding: 70px 163px 70px 133px;
  }

  @media (max-width: $vp-767) {
    padding: 70px 15px;
  }
}

.contacts__container h2 {
  font-weight: 600;
  font-family: $ff-alumni;
  font-size: 54px;
  line-height: 70px;
  letter-spacing: 0.03em;
  margin: 0;
  margin-bottom: 34px;

  @media (max-width: $vp-1199) {

  }

  @media (max-width: $vp-767) {
    margin-bottom: 50px;
  }
}

.contacts__list {
  margin: 0;
  padding: 0;
  list-style: none;
  padding-left: 3px;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-template-rows: repeat(2, 1fr);
  column-gap: 120px;
  row-gap: 15px;
  position: relative;

  &::before {
    content: "";
    width: 35%;
    height: 1px;
    background-color: $color-line-blue;
    position: absolute;
    bottom: 50%;
    left: 33%;
    transform: rotate(90deg);
  }

  @media (max-width: $vp-1199) {

  }

  @media (max-width: $vp-767) {
    display: flex;
    flex-direction: column;
    gap: 35px;

    &::before {
      display: none;
    }
  }
}

.contacts__item h3 {
  margin: 0;
  font-weight: 700;
  font-size: 16px;
  margin-bottom: 3px;
}

.contacts__item p {
  margin: 0;
  font-size: 14px;
  line-height: 21px;
}

.contacts__item a {
  margin: 0;
  font-size: 14px;
  line-height: 21px;
  cursor: pointer;

  &:hover {
    color: $color-red-line;
  }

  &:focus {
    outline: none;
    color: $color-red-line;
  }
}
